import React, {FunctionComponent} from 'react';
import {Container, Header, Content, Item, ValueNumber, Label, Button} from './style';
import {CalculatorType} from '../calculator/calculator';

interface OfferProps {
  type: CalculatorType,
  loanAmount: number,
  interestRate: number,
  monthPayment: number,
  minSalary: number,
  onClick: () => void
}

const Offer: FunctionComponent<OfferProps> = ({type, loanAmount, interestRate, monthPayment, minSalary, onClick}) => {
  const loanAmountLabel = type === ` ` ? ` ` : ` `;
  const percent = interestRate.toFixed(2).replace(`.`, `,`);

  return (
    <Container>
      <Header> </Header>
      <Content>
        <Item>
          <ValueNumber>{`${loanAmount.toLocaleString()} `}</ValueNumber>
          <Label>{loanAmountLabel}</Label>
        </Item>
        <Item>
          <ValueNumber>{percent}%</ValueNumber>
          <Label> </Label>
        </Item>
        <Item>
          <ValueNumber>{`${monthPayment.toLocaleString()} `}</ValueNumber>
          <Label> </Label>
        </Item>
        <Item>
          <ValueNumber>{`${minSalary.toLocaleString()} `}</ValueNumber>
          <Label> </Label>
        </Item>
      </Content>
      <Button onClick={onClick}> </Button>
    </Container>
  );
};

export default Offer;
